import React from "react"
import { Behavior } from "gatsby-theme-ceteicean/src/components/Behavior"
// import { GatsbyImage } from "gatsby-plugin-image"
import Button from 'react-bootstrap/Button'
import OverlayTrigger from 'react-bootstrap/OverlayTrigger'
import Popover from 'react-bootstrap/Popover'
import { TEINodes } from "react-teirouter"

import "./note.css"

const Note = (props) => {

  const seg = props.teiNode
  const note = seg.lastElementChild
  const n = note ? note.getAttribute('n') || '' : 1
  const text = note.innerHTML
  const lemma = seg.firstElementChild.textContent ? seg.firstElementChild.textContent : "Anmerkung"

  const popover = (
    <Popover id="popover-basic">
      <Popover.Header as="h3">{ lemma }</Popover.Header>
      <Popover.Body>
        { text }
      </Popover.Body>
    </Popover>
  );

  return <Behavior node={props.teiNode}>
            <TEINodes 
                    teiNodes={props.teiNode.childNodes}
                    {...props}/>
            <OverlayTrigger trigger="click" placement="auto" overlay={popover}>
                <sup className="Note"><Button variant="link" className="popoverButton" style={{padding: 0, textDecoration: "none"}}>{ n }</Button></sup>
                </OverlayTrigger>
         </Behavior>
}

export default Note